import React, { useEffect } from "react";
import Axios from "axios";
import { useState } from "react";

import { Link } from "react-router-dom";
import logo from "../resources/logo.png"

import "./Styles/product-card.css"

function Dekoracijos() {
  const [ItemData, setData] = useState([]);

  const getData = (Category, ItemCount) => {

    setData([]); // set data to nothing so loading icon shows

    Axios.post("https://api.armilla.lt:8443/Products", {Category: Category, ItemCount: ItemCount}).then((response) => {
      const CategoryEl = document.getElementsByClassName("category")
      setData(response.data);
      for(var i = 0; i < CategoryEl.length; i++){
        // these if statements are kinda cancer but it works
        CategoryEl[i].classList = "category"
        if(Category === "All"){CategoryEl[0].classList = "category cSelected"}
        else if (Category === "Dekoracija"){CategoryEl[1].classList = "category cSelected"}
        else if(Category === "Naudinga"){CategoryEl[2].classList = "category cSelected"}
        else if(Category === "Meme"){CategoryEl[3].classList = "category cSelected"}
      }
    });
  };

  useEffect(() => {
    window.scrollTo(0, 0);
    getData("All", 69);
  }, []);

  return (
    <div className="ContentItems">
      <div id="ProductHeader">

        <h2><img src={logo} alt=""></img>rmilla</h2>
        <h3>Produktai</h3>
      </div>
      <div className="categories">
        <span className="category" onClick={() => getData("All", 69)}>Visi Produktai</span>
        <span className="category" onClick={() => getData("Dekoracija", 420)}>Dekoracijos</span>
        <span className="category" onClick={() => getData("Naudinga", 6969)}>Naudinga</span>
        <span className="category" onClick={() => getData("Meme", 69420)}>Meme</span>
      </div>
      {ItemData.length ? (
        <div className="ItemDiv">
        {ItemData.map((ItemData, index) => (
          <div className="item-card">
            <div className="box-up">
              <img className="img" src={ItemData.Materials[ItemData.selectedMaterial].Colors[ItemData.selectedColor].Image} alt="" />
              <div className="img-info">
                <div className="info-inner">
                  <span className="p-name">{ItemData.Name}</span>
                </div>
                <div className="a-variant">Dydiai :
                {ItemData.Sizes.map((size) => (
                  <span className="bold"> {size}</span>
                ))}
                </div>
                <div className="a-variant a-materials">Mediagos: 
                {ItemData.Materials.map((mat, matIndex) => (
                  matIndex > 0 ? (<>, <span className="bold"> {mat.Name}</span></>):(
                  <span className="bold"> {mat.Name}</span>
                  )
                ))}
                </div>
              </div>
            </div>

            <div className="box-down">
              <div className="h-bg">
                <div className="h-bg-inner"></div>
              </div>

              <Link className="cart" to={"/products?id=" + ItemData.ID}>
                <span className="price">{ItemData.Price + ItemData.Materials[ItemData.selectedMaterial].Colors[ItemData.selectedColor].Extra} </span>
                <span className="add-to-cart">
                  <span className="txt">Perirti</span>
                </span>
              </Link>
            </div>
          </div>
        ))}
        </div>
      ):(
      <div className="ItemDiv flex">
        <div className="lds-ripple"><div></div><div></div></div>
      </div>)}
    </div>
  );
}

export default Dekoracijos;
