import React, {useEffect} from "react";
import { NavLink } from "react-router-dom";
import logo from "../resources/logo.png"

function Footer() {

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div id="footer">
    <div className="flex">
      <div className="Armilla">
        <img src={logo} alt=""></img>
        Armilla
        <p>Dirbame jums nuo 2022 m.</p>
        </div>
      <div className="Nav">
        <NavLink className="NavBtn" to="/">Namai</NavLink>
        <NavLink className="NavBtn" to="/produktai">Produktai</NavLink>
        <NavLink className="NavBtn" to="/contact">Usakymai</NavLink>
        <NavLink className="NavBtn" to="/about">Apie Mus</NavLink>
        <NavLink className="NavBtn" to="/contact">Kontaktuoti</NavLink>
      </div>
      <div className="Nav">
        <a className="NavBtn" rel="noreferrer" href="https://armilla.lt/#/tos" >Paslaug teikimo slygos</a>
        <a className="NavBtn" rel="noreferrer" href="https://portal.termshub.io/armilla.lt/#website_tos" target="_blank">Privatumo politika</a>
        <a className="NavBtn" rel="noreferrer" href="https://portal.termshub.io/ocmzgech4n/#refund_policy/" target="_blank">Grinimo politika</a>
      </div>
    </div>
    <div id="copyright">2022  Armilla.lt</div>
  </div>
  );
}

export default Footer;
