import React, {useEffect} from "react";
import "aos/dist/aos.css";
import "./Styles/About.css";
import AOS from "aos";

import { mobile } from "./Navigation";

function About() {

  useEffect(() => {
    window.scrollTo(0, 0);
    AOS.init();
    AOS.refresh();
  }, []);

  function getDelay(ID, time){
    if(mobile){
      return 0
    } else {
      return (ID * time)
    }
  }

  return (
    <div className="AppAbout">
      <div className="Container">
        <div className="about flex">
          <h1 data-aos="fade-down" data-aos-delay={getDelay(0, 0)} data-aos-duration="500">Armilla</h1>
          <p data-aos="fade-up" data-aos-delay={getDelay(1, 100)} >Elektronin parduotuv, teikianti jums galimyb rinktis i daugybs aukiausios kokybs produkt.
          </p>
          <h5 data-aos="fade-up" data-aos-delay={getDelay(1, 200)}>K daro Armilla?</h5>
          <p data-aos="fade-up" data-aos-delay={getDelay(1, 250)}>Armilla gamina aukiausios kokybs vairi mediag ir paskiri produktus, tokius kaip dekoracijas,
          naudingus daiktus, io metu usiimame tik 3D spausdintais produktais, taiau artimoje ateityje tikims svetain papildyti ir "Acrylic" mediagos produkt gamyba, 
          su kuria iuo metu eksperimentuojame. Tolimesnje ateityje planuojame eksperimentuoti su metalo liejimu.
           </p>
          <h5 data-aos="fade-up" data-aos-delay={getDelay(1, 300)}>Kodl Armilla yra geriausia ioje srityje?</h5>
            <p data-aos="fade-up" data-aos-delay={getDelay(1, 350)}>Mes klausoms js pastab ir rekomendacij, todl ms produktais tikrai nenusivilsite.
            Turime komand aktyvi ir motyvuot moni, kurie nuolat galvoja kaip galtume gerinti gamybos proces, produkt kokyb. Inovuojame eksperimentuodami
            su skurtingomis mediagomis, gamybos procesais, turime dedikuot 3D modeliuotoj, kuris moni idjas paveria realybe.</p>
          <h5 data-aos="fade-up" data-aos-delay={getDelay(1, 400)}>Kas numatyta Armilla ateityje</h5>
            <p data-aos="fade-up" data-aos-delay={getDelay(1, 450)}>Artimoje ateityje tikims prie ms katalogo pridti ir "Acrylic" mediagos produktus, su ia mediaga
            uo metu eksperimentuojame, turjome idj su vairi form ilietu maistu - guminukais, ele. Tolimesnje ateityje planuojame eksperimentuoti su metalo perdirbimu ir liejimu, ir, jei pasiseks, prijungti metalo gaminius prie 
            katalogo.</p>
          <h5 data-aos="fade-up" data-aos-delay={getDelay(1, 500)}>Kodl turtumte pasitikti Armilla</h5>
            <p data-aos="fade-up" data-aos-delay={getDelay(1, 550)}>Mes klausoms js pastab ir rekomendacij, todl ms produktais tikrai nenusivilsite. Jei nutinka taip, kad 
            produktas pas jus nusigavo paeistas, ar juo tikrai esate nusivyl, mes teikiame 7 dien garantij, per kuri pinigus u produkt galime grainti.</p>
          <div id="policies">
              <a href="/tos">Paslaug teikimo slygos</a>
            | <a href="https://portal.termshub.io/armilla.lt/#website_tos" target="_blank">Privatumo politika</a>
            | <a href="https://portal.termshub.io/ocmzgech4n/#refund_policy/" target="_blank">Grinimo politika</a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default About;
